body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1576px) {
  :root {
    font-size: 18px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1735px) {
  :root {
    font-size: 19.3px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 1921px) {
  :root {
    font-size: 22px;
    transition: font-size .3s ease;
  }
}

@media screen and (min-width: 2140px) {
  :root {
    font-size: 24px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1575px) {
  :root {
    font-size: 16.4px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 1440px) {
  :root {
    font-size: 16px;
    transition: font-size .3s ease;
  }
}

@media screen and (max-width: 375px) {
  :root {
    font-size: 12px;
    transition: font-size .3s ease;
  }
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Axiforma-Regular.woff2") format("woff2"), url("../fonts/Axiforma-Regular.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Axiforma-Bold.woff2") format("woff2"), url("../fonts/Axiforma-Bold.woff") format("woff");
}

@font-face {
  font-family: 'Axiforma';
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/Axiforma-Black.woff2") format("woff2"), url("../fonts/Axiforma-Black.woff") format("woff");
}

body {
  font-family: 'Axiforma', sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

source {
  width: 0;
  height: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.header {
  height: 3.125rem;
  margin: 0 8.563rem 0 1.25rem;
  padding-top: 2rem;
  padding-bottom: 0.625rem;
  position: relative;
  z-index: 50;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  max-width: 90rem;
}

.header svg {
  height: 100%;
}

.header a {
  font-size: 1.125rem;
  font-weight: 700;
  color: #d40f14;
  height: inherit;
  display: flex;
  align-items: center;
  text-decoration: none;
}

.header__logo {
  height: inherit;
  display: flex;
  z-index: 10;
}

.header__logo svg {
  width: 11.5rem;
}

.header__navigation {
  width: 100%;
  margin: 0 2.375rem 0 3.313rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}

.header__contacts, .header__for {
  width: 50%;
  height: inherit;
  display: flex;
}

.header__contacts a:not(:first-child), .header__for a:not(:first-child) {
  margin-left: 2.188rem;
}

.header-contacts {
  justify-content: flex-end;
}

.header-contacts svg {
  width: 1.125rem;
  margin-right: 0.625rem;
}

@media screen and (max-width: 375px) {
  .header {
    width: 100%;
    margin-right: 0;
  }
  .header__navigation {
    display: none;
  }
  .header__for {
    width: 100%;
    flex-direction: column;
  }
  .header__for a:last-child {
    margin-left: 0;
    margin-top: 0.833rem;
  }
  .header__contacts {
    display: none;
  }
}

.sidebar {
  position: fixed;
  width: 7.375rem;
  height: 100vh;
  border-left: 0.063rem solid #d40f14;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  z-index: 5;
}

.sidebar__blank {
  width: 7.375rem;
  height: 6.5rem;
}

.sidebar__request {
  width: 5.438rem;
  height: 5.438rem;
  background-color: #d40f14;
  border-radius: 50%;
  font-size: 0.75rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 0 0 0 black;
  transform: scale(1);
  animation-play-state: paused;
}

.sidebar__request:hover {
  cursor: pointer;
  animation: pulse 2s infinite;
}

.sidebar__request p {
  margin: 0;
  height: 2.125rem;
}

.sidebar__arrow-top {
  margin-bottom: 6.875rem;
}

.sidebar__arrow-top svg {
  height: 2.75rem;
  width: 2.75rem;
}

.sidebar__arrow-top .arrow-use path {
  animation: arrow-top 2s linear infinite;
}

.sidebar__arrow-top:hover {
  cursor: pointer;
}

.burger-wrapper {
  width: 7.375rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 52;
}

.burger-wrapper .burger {
  position: fixed;
  z-index: 10;
  top: 2rem;
  width: 2.75rem;
  height: 2.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.125rem solid #d40f14;
  border-radius: 50%;
  transition: border-color 0.4s ease;
}

.burger-wrapper .burger:hover {
  cursor: pointer;
}

.burger-wrapper .burger span {
  position: absolute;
  content: '';
  max-width: 1.688rem;
  width: 100%;
  height: 0.125rem;
  display: block;
  background-color: #d40f14;
  transition: transform 0.4s ease, background-color 0.4s ease, opacity 0.2s ease, margin 0.5s ease;
}

.burger-wrapper .burger span:first-child {
  margin-bottom: 0.875rem;
}

.burger-wrapper .burger span:last-child {
  margin-top: 0.875rem;
}

.dropdown {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #d40f14;
  top: 0;
  left: 0;
  transform: translateY(-250%);
  transition: transform 0.4s ease-in-out;
  z-index: 51;
}

.dropdown.down {
  transform: translateY(0);
  transition: transform 0.4 ease-in-out;
}

.dropdown__header {
  display: flex;
  align-items: center;
  height: 3.125rem;
  margin: 2rem 8.563rem 0 1.25rem;
}

.dropdown__header svg {
  width: 11.5rem;
  height: inherit;
}

.dropdown__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 80%;
  margin-top: 2.875rem;
}

.dropdown__item {
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.5rem;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke: 2px #fff;
  transition: color 0.4s ease;
}

.dropdown__item:not(:first-child) {
  margin-top: 2rem;
}

.dropdown__item:hover {
  cursor: pointer;
  transition: color 0.4s ease;
  color: #fff;
}

.dropdown__right-side {
  position: absolute;
  right: 0;
  top: 0;
  width: 7.375rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.dropdown__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 17.875rem;
}

.dropdown__social :not(:first-child) {
  margin-top: 0.938rem;
}

.dropdown__social a {
  width: 2.5rem;
  height: 2.5rem;
}

.dropdown__social svg {
  width: inherit;
  height: inherit;
  border-radius: 1.25rem;
  box-shadow: 0.25rem 0.25rem 0.938rem rgba(0, 0, 0, 0.2);
}

.dropdown__contacts {
  width: 13.375rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-bottom: 5.7rem;
  margin-right: 25.75rem;
}

.dropdown__contacts a {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.875rem;
  text-decoration: none;
  color: #fff;
}

.dropdown__contacts svg {
  width: 1.125rem;
  height: 1.125rem;
  margin-right: 0.625rem;
}

.center-line {
  position: fixed;
  z-index: -4;
  height: 100vh;
  top: 0;
  left: 50%;
  border-right: 0.063rem solid #d40f14;
}

@keyframes arrow-top {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0.7);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(215, 15, 22, 0);
  }
  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(215, 15, 22, 0);
  }
}

@media screen and (max-width: 375px) {
  .sidebar {
    height: 100%;
    width: 4.133rem;
  }
  .sidebar__blank {
    width: 4.133rem;
  }
  .sidebar__request {
    width: 3.138rem;
    height: 3.138rem;
  }
  .sidebar__request p {
    font-size: .45rem;
    height: 1.5rem;
  }
  .burger-wrapper {
    width: 4.133rem;
  }
  .burger-wrapper .burger span {
    height: 2px;
  }
  .center-line {
    height: 100%;
  }
}

.left-side-bar {
  position: fixed;
  width: 7.375rem;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 0;
  background-color: #fff;
  border-right: 0.063rem solid #d40f14;
}

@media screen and (max-width: 375px) {
  .left-side-bar {
    width: 3.333rem;
    height: 100%;
  }
}

.main-info {
  width: calc(100% - 14.75rem);
  margin: 0 auto;
  display: flex;
}

.main-info__left, .main-info__right {
  width: 50%;
}

.main-info__left {
  margin: 3.25rem 0 0 1.25rem;
}

.main-info__right {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 7rem 1.5rem 0 0;
}

.main-info__bottom-img, .main-info__top-img {
  position: absolute;
  width: 28.125rem;
  border-radius: 1.25rem;
  background-color: gray;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
}

.main-info__bottom-img {
  right: 1.5rem;
}

.main-info__top-img {
  top: 25.75rem;
  left: 1.25rem;
}

.main-info__title {
  max-width: 35.75rem;
  font-size: 2.25rem;
  font-weight: 700;
  line-height: 2.75rem;
  text-transform: uppercase;
  color: #d40f14;
  margin-top: 1.813rem;
}

.main-info__description {
  max-width: 28.625rem;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.625rem;
  color: #111;
  margin-top: 2.25rem;
}

.main-info__description:first-child {
  margin-top: 4.375rem;
}

.main-info__all-events {
  text-decoration: none;
  color: #d40f14;
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.875rem;
  display: block;
  position: relative;
  padding-left: 7.375rem;
}

.main-info__all-events::before {
  position: absolute;
  content: '';
  width: 5.938rem;
  height: 0.125rem;
  background-color: #d40f14;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}

.info-price {
  width: calc(100% - 14.75rem);
  margin: 6.125rem auto 0 auto;
  display: flex;
}

.info-price__left, .info-price__right {
  width: 50%;
}

.info-price__left {
  position: relative;
  padding-left: 1.5rem;
}

.info-price__bottom-img, .info-price__top-img {
  position: absolute;
  width: 28.125rem;
  border-radius: 1.25rem;
  background-color: gray;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
}

.info-price__top-img {
  top: 18.85rem;
  left: 8.75rem;
}

.info-price__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  margin-right: 1.063rem;
}

.info-price__description {
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.625rem;
  color: #111;
  margin-top: 2.25rem;
}

.info-price__description-wrapper {
  max-width: 28.625rem;
}

.info-price__description:first-child {
  margin-top: 4.375rem;
}

.info-price__price {
  font-size: 2.25rem;
  line-height: 2.75rem;
  color: #111;
  text-transform: uppercase;
  word-wrap: break-word;
  max-width: 16.25rem;
  align-self: flex-start;
  margin-top: 4.063rem;
}

.info-price__price span {
  text-transform: none;
  color: #d40f14;
}

.offer-card-price {
  width: calc(100% - 14.75rem);
  margin: 8.375rem auto 0 auto;
  display: flex;
  justify-content: center;
}

.offer-card-price__card {
  max-width: 35.125rem;
  max-height: 26.25rem;
  padding: 2.188rem 3.688rem 4.563rem 4.313rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 1.25rem;
  background-color: #d40f14;
  box-shadow: 0.375rem 0.375rem 1.25rem rgba(0, 0, 0, 0.25);
}

.offer-card-price__card .card__name, .offer-card-price__card .card__email {
  font-size: 1.5rem;
  line-height: 2.438rem;
  color: #fff;
  font-weight: 700;
}

.offer-card-price__card .card__email {
  margin-top: 1.25rem;
}

.offer-card-price__card .card__distribution {
  margin-top: 1.625rem;
}

.offer-card-price__card .card__consent {
  margin-top: 0.75rem;
}

.offer-card-price__card .card__consent a {
  margin-left: 4px;
  color: #fff;
}

.offer-card-price__card .card__distribution, .offer-card-price__card .card__consent {
  font-size: 0.75rem;
  line-height: 1.188rem;
  color: #fff;
}

.offer-card-price__card .card__distribution:hover, .offer-card-price__card .card__consent:hover {
  cursor: pointer;
}

.offer-card-price__card .card__distribution input[type='checkbox'], .offer-card-price__card .card__consent input[type='checkbox'] {
  position: absolute;
  z-index: -10;
  opacity: 0;
}

.offer-card-price__card .card__distribution span, .offer-card-price__card .card__consent span {
  display: inline-flex;
  user-select: none;
}

.offer-card-price__card .card__distribution span::before, .offer-card-price__card .card__consent span::before {
  content: '';
  display: inline-block;
  width: 0.75rem;
  height: 0.75rem;
  border: 0.063rem solid #fff;
  margin-right: 0.5em;
  background-color: #fff;
}

.offer-card-price__card .card__distribution input:not(:disabled):active + span::before, .offer-card-price__card .card__consent input:not(:disabled):active + span::before {
  background-color: #ffb3b3;
