.left-side-bar {
    position: fixed;
    width: 7.375rem;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 0;
    background-color: #fff;
    border-right: .063rem solid $color-main;

    @media screen and (max-width: 375px) {
        width: 3.333rem;
        height: 100%;
    }
}